import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';
import './sass/main.scss';

import progressBar from './js/modules/progressBar';


 const doId = () => {
   return Math.random().toString(36).substring(2, 16);
 };

 let tasks = {
   // LINK task
   lastNumber: 4,
   current: [
     {
       taskId: doId(),
       taskContent: ' 1',
       taskState: 'current',
       taskColor: 'bg-warning',
       taskTime: 1,
       taskProgress: false,
     },
     {
       taskId: doId(),
       taskContent: ' 2',
       taskState: 'current',
       taskColor: 'bg-info',
       taskTime: 6,
       taskProgress: false,
     },
     {
       taskId: doId(),
       taskContent: ' 3',
       taskState: 'current',
       taskColor: 'bg-success',
       taskTime: 8,
       taskProgress: false,
     },
     {
       taskId: doId(),
       taskContent: ' 4',
       taskState: 'current',
       taskColor: 'bg-dark',
       taskTime: 3,
       taskProgress: false,
     },
   ],
   done: [
     {
       //
       taskId: doId(),
       taskContent: ' 3',
       taskState: 'done',
     },
   ],

   get fullTime() {
     let fullTime = 0;
     this.current.forEach((key) => {
       if (key.taskProgress) {
         fullTime += key.taskTime;
       }
     });
     return fullTime;
   },
   delTask(id) {
     this.current.forEach((el, i) => {
       if (el.taskId == id) {
         this.current.splice(i, 1);
       }
     });
   },
   addTaskProgressNumber() {
     this.lastNumber++;
     return this.lastNumber;
   },
 };

 let sel = {
   sectionTimeLine: '.m_timeLine',
   progressIndicator: '.m_progressIndicator',
   progressBar: '.m_progressBar',
   taskList: '.m_taskList',
   colorList: '.m_colorList',
   timeChoice: '.m_timhoice',
   btnModalAddTask: '.m_btnModalAddTask',
   getElbySeclector(key, oneOrAll = 'one') {
     if (oneOrAll == 'one') return document.querySelector(this[key]);
     if (oneOrAll == 'all') return document.querySelectorAll(this[key]);
   },
 };


 progressBar(tasks, sel.progressBar);